import { Validation } from './validation';

export const ValidationPassword: Validation[] = [
  new Validation(
    (v) => (
    //   new RegExp(/^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?\d)[A-Za-z\d]{8,}$/).test(v)
    // ),
    // ', (), ()',

      new RegExp(/^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?\d)[A-Za-z\d]{8,16}$/).test(v) || !v.length
    ),
    ', (), ()',
  ),
];
