import { Validation } from './validation';

export const ValidationTel: Validation[] = [
  // new Validation(
  //   (v) => Boolean(!Number.isNaN(Number(v))),
  //   '',
  // ),
  // new Validation(
  //   (v) => !v || v.length === 10 || v.length === 11,
  //   '',
  // ),
  new Validation(
    (v) => !v || v.length === 10
      || v.length === 11
      || v.length === 12
      || v.length === 13,
    '',
  ),
  new Validation(
    (v) => Boolean(
      !v || v.match(/^[0-9\\-]+$/),
    ),
    '-',
  ),
];
