/* eslint-disable no-irregular-whitespace */
import './change-password-error.pc.scss';
import { Link } from 'react-router-dom';
import React from 'react';
import { useDispatch } from 'react-redux';
import { push } from 'connected-react-router';
import { RoutingPath } from '../../../../../routes/routing-pass';
import { Button } from '../../../../ui/button/button';

export const ChangePasswordErrorPC = () => {
  const dispatch = useDispatch();
  return (
    <div className="loginPC">
      <div className="loginPC__body">
        <div className="loginPC__logo"></div>
        <div className="loginPC__title"></div>
        <div className="text_box">
          URL<br />
          URL24<br />
          <br />
          <br />
          <br />
          
        </div>
        <div style={{ margin: '0 0 30px' }}>
          <Button
            type="submit"
            onClick={() => dispatch(push(RoutingPath.sendPassword))}
          >
          </Button>
        </div>
      </div>
      <div className="link_box">
        <Link to={RoutingPath.login}><i className="fas fa-arrow-left" />&nbsp;</Link>
      </div>
    </div>
  );
};
