export class RouteModel {
  static totalDurationDistance(
    legs: globalThis.google.maps.DirectionsLeg[],
  ):{
    duration: string,
    distance: string,
  } {
    let durationText = ''; // 
    let distanceText = ''; // 

    /*
    * durationvalue
    * distancevalue
    */
    // console.groupCollapsed('LEGS: ', legs);
    // console.groupEnd();

    // ----------------------------------------------------------------------------

    let totalSeconds = 0; // 
    let totalMinutes = 0;
    let totalHours = 0;

    let minute: number = 0;
    let hour: number = 0;
    let day: number = 0;

    let totalMeter: number = 0;
    let totalKilometer: number = 0;

    legs.forEach((v) => {
      // 
      totalSeconds += v.duration?.value ?? 0;
      // (m)
      totalMeter += v.distance?.value ?? 0;
    });

    /*
     * 
     */
    // console.groupCollapsed(': ', totalSeconds, '');

    // 
    totalMinutes = Math.round(totalSeconds / 60);
    // console.log(' L ', totalMinutes, ' ()');

    /* 1 */
    if (totalMinutes > 59) {
      totalHours = Math.floor(totalMinutes / 60);
      minute = totalMinutes % 60;
      if /* 24 */ (totalHours < 25) {
        hour = totalHours;
        // console.log('   L ', hour, ' ', minute, '');
      } /* 1 */ else {
        day = Math.floor(totalHours / 24);
        hour = totalHours % 24;
        // console.log('   L ', day, ' ', hour, '');
        // console.log('   L ', day, ' ', hour, ' ', totalMinutes % 60, ' ()');
      }
    } /* 1 */ else {
      minute = totalMinutes;
      // console.log('    ', minute, '');
    }

    // console.log('day', 'hour, minute', day, hour, minute);

    if (day) {
      durationText = `${day}  ${hour} `;
    } else if (hour) {
      durationText = `${hour}  ${minute} `;
    } else {
      durationText = `${minute} `;
    }

    // console.groupEnd();

    /*
     * 
     * 100m100mkm
     * 100m(0.1km)100km
     * 100km()
     */
    // console.groupCollapsed(': ', totalMeter, 'M');

    /* 100m */
    if (totalMeter < 100) {
      distanceText = `${totalMeter} m`;
      // console.log('', totalMeter);
    } /* 100m */ else {
      totalKilometer = totalMeter < 100000
        ? Math.round((totalMeter / 1000) * 10) / 10
        : Math.round((totalMeter / 1000));
      // console.log(' L ', totalKilometer, 'km');
      distanceText = `${totalKilometer} km`;

      // 100km0.0
      if (totalKilometer < 100 && !(totalKilometer - Math.floor(totalKilometer))) {
        distanceText = `${totalKilometer}.0 km`;
      }

      // console.groupEnd();
    }

    // console.groupCollapsed('');
    // console.log(' >>> ', durationText);
    // console.log(' >>> ', distanceText);

    return ({
      duration: durationText,
      distance: distanceText,
    });
  }
}
