const colorSet = {
  black: 'color:#000000',
  red: 'color:#ff3c00',
  green: 'color:#90ee90',
  yellow: 'color:#ffffe0',
  blue: 'color:#add8e6',
  white: 'color:#ffffff',
};
type Colors = 'black' | 'red' | 'green' | 'yellow' | 'blue' | 'white';
/**
 * console.log  
 *
 *
 *  < >  ( 11 )
 *
 * ) console.log(...LogDecorator(`piyoyo<hoge>pipi`, 'blue'));
 *
 * @param str 
 * @param color 
 */
export const LogDecorator = (str: string, color: Colors) => [str.replace('<', '%c').replace('>', '%c'), colorSet[color], ''];
