const week = ['', '', '', '', '', '', ''];

/**  */
export const getNowDate = () => {
  const today = new Date();
  return {
    date: today,
    year: today.getFullYear(),
    month: today.getMonth() + 1,
    day: today.getDate(),
    dayOfWeek: week[today.getDay()],
    hour: today.getHours(),
    min: today.getMinutes(),
    seconds: today.getSeconds(),
  };
};
